import './App.css';
import { Container, Flex, Grid, useMediaQuery } from "@chakra-ui/react"
import Sidebar from './partials/Sidebar';
import MainBody from './partials/MainBody';
import MobileMenu from './partials/MobileMenu';
import MobileBanner from './partials/MobileBanner';
import MobileCardLayout from './partials/MobileCardLayout';
import MobilePrintInvoiceCards from './partials/MobilePrintInvoiceCards';

function App() {

  const [lowerThan992] = useMediaQuery("(max-width: 992px)")

  const mainBodyStyling = {
    bgGradient: "linear(to-r,white,gray.200)", outline: "10px solid black", ml: "12", mr: "12", align: "flex-start", rounded: "50px", minH: "max-content", pr: "7", mt: "12", pt: "5", pb: "0", mb: "12",
  }

  const mobileBodyFlexStyleProp = {
    bgGradient: "linear(to-r,white,gray.200)", outline: "10px solid black", mt: "5", mb: "5", pt: "10", pb: "10", rounded: "35", flexDirection: "column"
  }

  return (
    lowerThan992 ? (
      <Container maxW="container.xl">
        <Flex {...mobileBodyFlexStyleProp}>
          <MobileMenu />
          <MobileBanner />
          <MobileCardLayout />
          <MobilePrintInvoiceCards />
        </Flex>
      </Container>
    ) : (
      <Container maxW="container.xl" >
        <Flex {...mainBodyStyling} >
          <Grid templateColumns="10% 90%" w="100%" >
            <Sidebar />
            <MainBody />
          </Grid>
        </Flex>
      </Container >
    )
  );
}

export default App;
